'use strict';

// MODULES //

import { ndarray as sdssumpw } from '@stdlib/blas-ext-base-sdssumpw';


// MAIN //

/**
* Computes the sum of single-precision floating-point strided array elements using extended accumulation.
*
* @param {PositiveInteger} N - number of indexed elements
* @param {Float32Array} x - input array
* @param {integer} strideX - stride length
* @param {NonNegativeInteger} offsetX - starting index
* @returns {number} sum
*
* @example
* import Float32Array from '@stdlib/array-float32';
*
* var x = new Float32Array( [ 2.0, 1.0, 2.0, -2.0, -2.0, 2.0, 3.0, 4.0 ] );
*
* var v = sdssum( 4, x, 2, 1 );
* // returns 5.0
*/
function sdssum( N, x, strideX, offsetX ) {
	return sdssumpw( N, x, strideX, offsetX );
}


// EXPORTS //

export default sdssum;
